<template>
  <div class="page-owasp-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/sys-report-conf' }">系统配置</el-breadcrumb-item>
        <el-breadcrumb-item>日志查询配置</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-form :model="sysReportConfForm" :rules="rules" ref="sysReportConfForm" label-width="180px">
          <el-form-item label="日志查询配置" key="1">
            <el-switch v-model="sysReportConfForm.report_conf" active-value="true" inactive-value="false">
            </el-switch>
          </el-form-item>
          <div v-if="sysReportConfForm.report_conf == 'true'">

            <el-form-item label="ClickHouse服务器地址" prop="report_conf_ch_host" key="3">
              <el-input v-model="sysReportConfForm.report_conf_ch_host" placeholder="请输入">
              </el-input>
            </el-form-item>
            <el-form-item label="ClickHouse服务器端口" prop="report_conf_ch_port" key="4">
              <el-input v-model="sysReportConfForm.report_conf_ch_port" placeholder="请输入">
              </el-input>
            </el-form-item>
            <el-form-item label="用户名" prop="report_conf_ch_user" key="2">
              <el-input v-model="sysReportConfForm.report_conf_ch_user" placeholder="请输入">
              </el-input>
            </el-form-item>
            <el-form-item label="密码" prop="report_conf_ch_password" key="5">
              <el-input v-model="sysReportConfForm.report_conf_ch_password" placeholder="请输入">
              </el-input>
            </el-form-item>
            <el-form-item label="数据库名称" prop="report_conf_ch_database" key="6">
              <el-input v-model="sysReportConfForm.report_conf_ch_database" placeholder="请输入">
              </el-input>
            </el-form-item>
          </div>

        </el-form>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12"></el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickLogConfigSubmit('sysReportConfForm')" :loading="loading">保存
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      sysReportConfForm: {
        
      },
    };
  },
  computed: {
    rules() {
      return {
        report_conf_ch_host: [
          {
            required: true,
            message: '请输入ClickHouse服务器地址',
            trigger: ["blur", "change"],
          }
        ],
        report_conf_ch_port: [
          {
            required: true,
            message: '请输入ClickHouse服务器端口',
            trigger: ["blur", "change"],
          }
        ],
        report_conf_ch_user: [
          {
            required: true,
            message: '请输入用户名',
            trigger: ["blur", "change"],
          }
        ],
        report_conf_ch_password: [
          {
            required: true,
            message: '请输入密码',
            trigger: ["blur", "change"],
          }
        ],
        report_conf_ch_database: [
          {
            required: true,
            message: '请输入数据库名称',
            trigger: ["blur", "change"],
          }
        ],
        
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var getUrl = "/waf/waf_get_sys_report_conf_conf";

      JXAjax(
        "post",
        getUrl,
        {},
        function (response) {
          t.loadingPage = false;
          t.sysReportConfForm = response.data.message;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },

    onClickLogConfigSubmit(sysReportConfForm) {
      var t = this;
      var postUrl = "/waf/waf_edit_sys_report_conf_conf";
      this.$refs[sysReportConfForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.sysReportConfForm,
            function (response) {
              t.loading = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
  },
};
</script>
<style>
.page-owasp-wrap {
  max-width: 800px;
  min-width: 400px;
}
</style>
